@charset "UTF-8";
/* Default styles */
:root {
  --primary-500: #19003A;
  --primary-400: #26004d;
  --primary-300: #46134b;
  --primary-200: #5A206C;
  --primary-100: #9f40ff;
  --primary-150: #a52885;
  --primary-250: #832578;
  --primary-350: #420e50;
  --secondary-400: #1e1e20;
  --secondary-300: #72687d;
  --secondary-200: #d8d4f2;
  --secondary-100: #edf3ec;
}

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  list-style: none;
  text-decoration: none;
  font-weight: normal;
  color: var(--secondary-100);
  -webkit-tap-highlight-color: rgba(255, 255, 255, 0);
}

body {
  max-width: 100vw;
  min-height: 100vh;
  background: #000 url("/media/stars-001.png");
  overflow-x: hidden;
}
body::-webkit-scrollbar {
  width: 0.35em;
}
body::-webkit-scrollbar-track {
  background: var(--black-pearl);
  border-left: 1px solid hsla(111, 24%, 94%, 0.1);
}
body::-webkit-scrollbar-thumb {
  background: var(--lavender-web);
}

@font-face {
  font-family: "Bunken Tech Sans SC Wide W01Bd";
  font-display: swap;
  font-style: normal;
  src: url("/public/fonts/Bunken.woff2") format("woff2"), url("/public/fonts/Bunken.woff") format("woff");
}
@font-face {
  font-family: "Montserrat";
  font-display: swap;
  font-style: normal;
  src: url("/public/fonts/Montserrat.woff2") format("woff2");
}
@font-face {
  font-family: "Pirulen V2";
  src: url("/public/fonts/Pirulen.woff2") format("woff2"), url("/public/fonts/Pirulen.woff") format("woff");
}
@font-face {
  font-family: "BEYNO";
  src: url("https://db.onlinewebfonts.com/t/87e0d5d6ba27de27e759ab254219b6b1.eot");
  src: url("https://db.onlinewebfonts.com/t/87e0d5d6ba27de27e759ab254219b6b1.eot?#iefix") format("embedded-opentype"), url("https://db.onlinewebfonts.com/t/87e0d5d6ba27de27e759ab254219b6b1.woff2") format("woff2"), url("https://db.onlinewebfonts.com/t/87e0d5d6ba27de27e759ab254219b6b1.woff") format("woff"), url("https://db.onlinewebfonts.com/t/87e0d5d6ba27de27e759ab254219b6b1.ttf") format("truetype"), url("https://db.onlinewebfonts.com/t/87e0d5d6ba27de27e759ab254219b6b1.svg#BEYNO") format("svg");
}
h1 {
  font-family: "Pirulen V2";
  text-transform: uppercase;
}

h2 {
  font-family: "Pirulen V2";
}

body {
  font-family: "Montserrat";
}

a, button, textarea {
