.ico-nav {
  display: none;
}

.order-link {
  display: none;
}

nav {
  display: flex;
  justify-content: space-between;
  align-items: center;
  position: relative;
}

.logo a {
  height: auto;
  width: 85px;
  display: inline-block;
}
.logo a img {
  width: 100%;
  height: 100%;
}

.command {
  padding: 6px;
  border: 1px solid var(--secondary-100);
  border-radius: 1.5em;
  transition: 0.25s;
  cursor: pointer;
}
.command a {
  padding: 6px 12px;
}
.command:hover {
  background: var(--primary-350);
}

nav:has(.burger-menu input:checked) .burger-menu label :nth-child(1) {
  transform: translateY(3px) rotate(-45deg);
}
nav:has(.burger-menu input:checked) .burger-menu label :nth-child(2) {
  opacity: 0;
}
nav:has(.burger-menu input:checked) .burger-menu label :nth-child(3) {
  transform: translateY(-3px) rotate(45deg);
}
nav:has(.burger-menu input:checked) .nav-links {
  transform: translateX(0) scaleX(1);
  opacity: 1;
}

.burger-menu {
  display: none;
}
.burger-menu input {
  position: absolute;
  opacity: 0;
}
.burger-menu label {
  position: relative;
  cursor: pointer;
  opacity: 0.8;
  transition: 0.25s opacity ease-in-out;
}
.burger-menu label:hover {
  opacity: 1;
}
.burger-menu label::before {
  content: "";
  position: absolute;
  top: 10px;
  left: 0;
  width: 25px;
  height: 20px;
}
.burger-menu label > div {
  width: 25px;
  height: 3px;
  border-radius: 2px;
  background: var(--secondary-100);
  transition: 0.25s ease-in-out;
}
.burger-menu label :nth-child(1) {
  transform: translateY(-4px);
}
.burger-menu label :nth-child(2) {
  opacity: 1;
  transition-delay: 0.05s;
}
.burger-menu label :nth-child(3) {
  transform: translateY(4px);
  transition-delay: 0.1s;
}

@media (max-width: 768px) {
  header {
    position: fixed;
    border-bottom: 2px solid color-mix(in srgb, var(--secondary-100), transparent 85%);
    background: hsla(263, 69%, 5%, 0.75);
    z-index: 5;
    padding: 0.6666666667em 1em;
  }
  header::before {
    content: "";
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    backdrop-filter: blur(20px);
  }
  .nav-links {
    display: grid;
    position: absolute;
    gap: 1em 0;
    top: 66px;
    right: -8px;
    transform: translateX(25%) scaleX(0);
    transform-origin: top right;
    padding: 1em 1.75em 1em 1em;
    border: 2px solid color-mix(in srgb, var(--secondary-100), transparent 85%);
    border-radius: 1em;
    background: hsla(263, 69%, 5%, 0.75);
    transition: 0.15s;
  }
  .nav-links .link a {
    text-shadow: initial;
    color: initial;
  }
  .nav-links:hover .link {
    opacity: 0.5;
  }
  .nav-links::before {
    content: "";
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    backdrop-filter: blur(20px);
    border-radius: 1em;
    z-index: 1;
    transition: 2s;
  }
  .nav-links .ico-nav {
    display: block;
    filter: invert(1);
  }
  .nav-links .order-link {
    display: block;
  }
  .nav-links .link {
    display: flex;
    gap: 0.6666666667em;
    z-index: 2;
    cursor: pointer;
    transition: 0.25s;
  }
  .nav-links .link:hover {
    opacity: 1;
  }
  .command {
    display: none;
  }
  .burger-menu {
    display: block;
  }
}
