/* Home page | Hero section */
#hero {
  height: calc(100vh + 95px);
  width: 100vw;
  display: grid;
  align-items: center;
  background: url("/public/media/main-bg1.webp") center/cover no-repeat;
  box-shadow: inset 0px -35px 50px #020000;
}
#hero .content {
  min-width: 275px;
  width: 50%;
  margin: 5% 0 0 10%;
  z-index: 2;
}
#hero .content h1 {
  font-size: clamp(1.2rem, 4vw, 2.489rem);
  max-width: 17ch;
  margin: 0 0 8px;
  text-shadow: 0px 0px 25px #000, 0px 0px 50px #000;
}
#hero .content h1 span {
  color: var(--primary-100);
}
@media (max-width: 640px) {
  #hero .content h1 span {
    color: var(--secondary-100);
    text-shadow: 0px 0px 5px var(--primary-500);
  }
}
#hero .content p {
  max-width: 50ch;
  line-height: 24px;
}

.c-t-a {
  display: flex;
  gap: 1.5em;
  margin: 2em 0 0;
}
.c-t-a a {
  border: 1px solid white;
  padding: 8px 42px 8px 16px;
  border-radius: 1.75em;
  position: relative;
  transition: 0.25s ease-in-out background;
}
.c-t-a a:hover > span {
  transform: translateY(-2px) rotate(55deg);
}
.c-t-a a:hover {
  background: var(--primary-350);
}
.c-t-a span {
  position: absolute;
  right: 6px;
  top: 2px;
  filter: invert(1);
  transition: 0.25s ease-in all;
}

#reason {
  display: flex;
  gap: 2em;
  align-items: center;
  height: 100vh;
}

.fact {
  max-width: 40ch;
  min-height: 225px;
  padding: 1.75em;
  border-radius: 1.75em;
  position: relative;
  background: hsla(270, 69%, 5%, 0.75);
  backdrop-filter: blur(5px);
  border: 1px solid hsla(111, 24%, 94%, 0.5);
}
.fact::after {
  content: "";
  position: absolute;
  top: -1px;
  left: 50%;
  transform: translateX(-50%);
  height: 2px;
  width: 85%;
  background: #000;
  z-index: -1;
}
.fact h2 {
  width: max-content;
  position: absolute;
  top: -16px;
  left: 50%;
  transform: translateX(-50%);
}
.fact h3 {
  font-weight: 600;
  text-align: center;
  margin: 0 0 8px;
}
.fact p {
  text-align: justify;
  line-height: 25px;
}
@media (max-width: 475px) {
  .fact {
    min-height: 260px;
    margin-bottom: 3em;
  }
}

.pagination {
  display: flex;
  position: absolute;
  bottom: 12px;
  left: 50%;
  transform: translateX(-50%);
}
.pagination > div {
  height: 10px;
  width: 10px;
  margin: 0 4px;
  background: var(--secondary-100);
  border-radius: 50%;
  transition: 0.25s width ease-in, 0.25s border-radius ease-in;
  cursor: pointer;
}

.navigation div {
  position: absolute;
  top: 50%;
  transform: translateY(-50%);
  cursor: pointer;
}
.navigation div img {
  filter: drop-shadow(#040716 0px 0px 2px);
}
.navigation .left {
  left: -30px;
}
.navigation .right {
  right: -30px;
}

[slide-step] > .wrap {
  display: none;
}

[slide-step="1"] > .slides-1,
[slide-step="2"] > .slides-2,
[slide-step="3"] > .slides-3,
[slide-step="4"] > .slides-4,
[slide-step="5"] > .slides-5,
[slide-step="6"] > .slides-6,
[slide-step="7"] > .slides-7,
[slide-step="8"] > .slides-8,
[slide-step="9"] > .slides-9,
[slide-step="10"] > .slides-10,
[slide-step="11"] > .slides-11,
[slide-step="12"] > .slides-12 {
  display: block;
}

[slide-step="1"] > .pagination :nth-child(1),
[slide-step="2"] > .pagination :nth-child(2),
[slide-step="3"] > .pagination :nth-child(3),
[slide-step="4"] > .pagination :nth-child(4),
[slide-step="5"] > .pagination :nth-child(5),
[slide-step="6"] > .pagination :nth-child(6),
[slide-step="7"] > .pagination :nth-child(7),
[slide-step="8"] > .pagination :nth-child(8),
[slide-step="9"] > .pagination :nth-child(9),
[slide-step="10"] > .pagination :nth-child(10),
[slide-step="11"] > .pagination :nth-child(11),
[slide-step="12"] > .pagination :nth-child(12) {
  width: 20px;
  border-radius: 16px;
}

.wrap {
  animation: slideIn 0.5s ease-out;
}

@keyframes slideIn {
  from {
    opacity: 0;
    transform: translateX(15px);
  }
  to {
    opacity: 1;
    transform: translateX(0);
  }
}
