  width: 30px;
  border-radius: 50%;
  aspect-ratio: 1/1;
  z-index: 2;
  box-shadow: #fff 0px 0px 10px, #fff 0px 0px 10px, #fff 0px 0px 10px;
}
.nav-btn img {
  width: 100%;
  height: 100%;
  scale: 1.1;
  position: relative;
  bottom: -3px;
}
.nav-btn:hover {
  opacity: 1;
}
.nav-btn.next {
  right: 0;
  transform: rotate(90deg);
}
.nav-btn.pre {
  left: 0;
  transform: rotate(-90deg);
}
@media (max-width: 475px) {
  .nav-btn {
    top: 87%;
  }
  .nav-btn.next {
    right: 3%;
    transform: rotate(90deg);
  }
  .nav-btn.pre {
    left: 3%;
    transform: rotate(-90deg);
  }
}

[active-site] > div {
  display: none;
}

[active-site="1"] > .site-1,
[active-site="2"] > .site-2,
[active-site="3"] > .site-3,
[active-site="4"] > .site-4,
[active-site="5"] > .site-5 {
  display: initial;
}

.prev, .descrip, .responsive {
  animation: siteIn 0.5s ease-out;
}

@keyframes siteIn {
  from {
    transform: translateX(25px);
  }
  to {
    transform: translateX(0);
  }
}
